
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { dataStatus, dataPaket } from "./master-soal";
import { gf as $gf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = $gf.configGet();
    const configXwfu = $gf.configXwfu();
    const listStatus = dataStatus();
    const listPaket = dataPaket();

    // "id": 1474,
    // "paket": "cpns",
    // "jenis": "Seleksi Kompetensi Dasar",
    // "kategori": "Tes Wawasan Kebangsaan",
    // "bidang": "Nasionalisme",
    // "question": "Pembentukan konstitusi di Indonesia, menggunakan cara.\r\n",
    // "answer": "C",
    // "createdAt": "2021-02-20 00:00:00"

    const titleTable: Array<string> = [
      "Paket",
      "Jenis Soal",
      "Kategori Soal",
      "Bidang Soal",
      "Pertanyaan",
      "Kunci Jawaban",
      "Created",
      "#",
    ];

    const dataMS = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      paket: listPaket[0].value,
      kategori: "",
      listKategori: [],
      bidang: "",
      listBidang: [],
    });

    const getDataListMS = async () => {
      cash("#loadingBSadmin").show();

      const page = dataMS.page;
      const pageSize = dataMS.pageSize;
      const search = dataMS.search;
      const kategori = dataMS.kategori;
      const bidang = dataMS.bidang;
      axios
        .get(
          api.adminMasterSoal +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize +
            "&kategori=" +
            kategori +
            "&bidang=" +
            bidang
          , configGet
        )
        .then(async (result) => {
          $gf.gLoading.hide();
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataMS.data = rs;
          dataMS.paginate = $gf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataMS.paginate ", dataMS.paginate);
          cash("#loadingBSadmin").hide();
        })
        .catch((error) => {
          cash("#loadingBSadmin").hide();
          $gf.gLoading.hide();
          $gf.handleJwtExpired(error);
        });
    };

    const getListBidang = async () => {
      const page = "1";
      const pageSize = "10";
      const search = "";

      axios
        .get(
          api.adminBidangSoal +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          dataMS.bidang = rs[0].title;
          dataMS.listBidang = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.title,
              id: e.id,
              kode: e.kode,
            };
            return obj;
          });

          await getDataListMS();
        })
        .catch(async (error) => {
          $gf.gLoading.hide();
          await $gf.handleJwtExpired(error);
          await $gf.handleEds(error);
        });
    }

    const getListKategori = async () => {
      $gf.gLoading.show();
      axios
        .get(
          api.adminKategoriSoal,
          configGet
        )
        .then(async (result) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          dataMS.kategori = rs[0].id;
          dataMS.listKategori = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.kode,
              id: e.id,
              kode: e.kode,
            };
            return obj;
          });

          await getListBidang();
        })
        .catch(async (error) => {
          $gf.gLoading.hide();
          await $gf.handleJwtExpired(error);
          await $gf.handleEds(error);
        });
    }

    getListKategori();

    const changePS = () => {
      getListKategori();
    };

    const goToPage = (p: number) => {
      dataMS.page = p;
      getListKategori();
    };

    const nextPage = () => {
      dataMS.page = dataMS.page + 1;
      getListKategori();
    };

    const beforePage = () => {
      dataMS.page = dataMS.page - 1;
      getListKategori();
    };

    const lastPage = () => {
      dataMS.page = dataMS.paginate.totalPages;
      getListKategori();
    };

    return {
      salesReportFilter,
      titleTable,
      getDataListMS,
      dataMS,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listStatus,
      getListKategori,
    };
  },
  data() {
    return {
      // for modal data bs
      section: "add",
      msgSuccess: "",

      detailMS: {
        id: 0,
        kode: "",
        title: "",
        jmlSoal: "",
        isActive: "1",
        idCategory: "",
      } as any,

      exDataMS: {
        id: 0,
        kode: "",
        title: "",
        jmlSoal: "",
        isActive: "1",
        idCategory: "",
      },

      exDeleteMS: {
        id: 0,
        title: "",
      },

      listKodeKS: [],
    };
  },
  methods: {
    async sM(id: string) {
      $gf.showModal(id);
    },
    async hM(id: string) {
      $gf.hideModal(id);
    },
    async listKategoriSoalApi(callback: any): Promise<void> {
      $gf.gLoading.show();
      axios
        .get(
          api.adminJenisSoal,
          this.configGet
        )
        .then(async (result) => {
          $gf.gLoading.hide();
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          this.exDataMS.idCategory = rs[0].id;
          this.listKodeKS = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.id,
              id: e.id,
              kode: e.kode,
            };
            return obj;
          });

          await callback();
        })
        .catch(async (error) => {
          $gf.gLoading.hide();
          await $gf.handleJwtExpired(error);
          await $gf.handleEds(error);
        });
    },
    addBS() {
      this.section = "add";
      const showMKS = () =>{
        cash("#modal-data-bs").modal("show");
      };
      this.listKategoriSoalApi( showMKS );
    },
    addBSApi() {
      $gf.gLoading.show();
      const ex = this.exDataMS;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      params.append("title", ex.title);
      params.append("jmlSoal", ex.jmlSoal);
      params.append("isActive", ex.isActive);
      params.append("idCategory", ex.idCategory);

      axios
        .post(api.adminBidangSoal, params, this.configXwfu)
        .then(async (result) => {
          $gf.gLoading.hide();

          const ds = result.data.diagnostic;
          // console.log("diagnpstic add deu ", ds);

          ex.kode = "";
          ex.title = "";
          ex.jmlSoal = "";
          ex.isActive = "";
          ex.idCategory = "";

          await $gf.msgHandler(true, 'Good Job!', ds.message);
          await cash("#modal-data-bs").modal("hide");
          await this.getDataListMS();
        })
        .catch(async (error) => {
          $gf.gLoading.hide();
          await $gf.handleJwtExpired(error);
          await $gf.handleEds(error);
        });
    },
    editBSApi(data: any) {
      this.section = "edit";
      const showMKS = () =>{
        cash("#modal-data-bs").modal("show");
      };
      this.listKategoriSoalApi( showMKS );
      $gf.gLoading.show();

      axios
        .get(api.adminBidangSoal + data.id, this.configGet)
        .then(async (result) => {
          $gf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);
          const ex = this.exDataMS;
          ex.id = rs.id;
          ex.kode = rs.kode;
          ex.title = rs.title;
          ex.jmlSoal = rs.jmlSoal;
          ex.isActive = rs.isActive ? "1" : "0";
          ex.idCategory = rs.SoalCategory.id;

          await cash("#modal-data-bs").modal("show");
        })
        .catch(async (error) => {
          $gf.gLoading.hide();
          await $gf.handleJwtExpired(error);
          await $gf.handleEds(error);
        });
    },
    updatedBS(): void {
      $gf.gLoading.show();
      const ex = this.exDataMS;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      params.append("title", ex.title);
      params.append("jmlSoal", ex.jmlSoal);
      params.append("isActive", ex.isActive);
      params.append("idCategory", ex.idCategory);

      axios
        .put(api.adminBidangSoal + ex.id, params, this.configXwfu)
        .then(async (result) => {
          $gf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          ex.kode = "";
          ex.title = "";
          ex.jmlSoal = "";
          ex.isActive = "";
          ex.idCategory = "";

          await $gf.msgHandler(true, 'Good Job!', ds.message);
          await cash("#modal-data-bs").modal("hide");
          await this.getDataListMS();
        })
        .catch(async (error) => {
          $gf.gLoading.hide();
          await $gf.handleJwtExpired(error);
          await $gf.handleEds(error);
        });
    },
    detailBSApi(data: any): void {
      $gf.gLoading.show();

      axios
        .get(api.adminBidangSoal + data.id, this.configGet)
        .then(async (result) => {
          $gf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          const ex = this.detailMS;
          ex.id = rs.id;
          ex.kode = rs.kode;
          ex.title = rs.title;
          ex.jmlSoal = rs.jmlSoal;
          ex.isActive = rs.isActive ? "Active" : "Suspend";
          ex.idCategory = rs.SoalCategory.title;

          await cash("#modal-detail-bs").modal("show");
        })
        .catch(async (error) => {
          $gf.gLoading.hide();
          await $gf.handleJwtExpired(error);
          await $gf.handleEds(error);
        });
    },
    showPass(id: string) {
      const idHtml = cash("#"+ id);
      if (idHtml.attr("type") === "password") {
        idHtml.attr("type", "text");
      } else {
        idHtml.attr("type", "password");
      }
    },
    deleteBSApi(data: any): void {
      const ex = this.exDeleteMS;
      ex.id = data.id;
      ex.title = data.title;

      cash("#delete-modal-data-bs").modal("show");
    },
    fixDeleteBS() {
      const ex = this.exDeleteMS;

      $gf.gLoading.show();

      axios
        .delete(api.adminBidangSoal + ex.id, this.configGet)
        .then(async (result) => {
          $gf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          ex.id = 0;
          ex.title = "";

          await $gf.msgHandler(true, 'Good Job!', ds.message);
          await cash("#delete-modal-data-bs").modal("hide");
          await this.getDataListMS();
        })
        .catch(async (error) => {
          $gf.gLoading.hide();
          await $gf.handleJwtExpired(error);
          await $gf.handleEds(error);
        });
    }
  },
});
